<template>
  <div class="warp">
    <div class="container">
      <div class="justify-content-center">
        <div class="map-container justify-content-center d-flex">
          <img :src="blankMapSrc" class="map-img m-5" alt="">
          <img :src="dapingSrc" class="daping scene-img" alt="" @click="goToTargetPage(14)">
          <img :src="dutiangongSrc" class="dutiangong scene-img" alt="" @click="goToTargetPage(9)">
          <img :src="fossilSrc" class="fossil scene-img" alt="" @click="goToTargetPage(11)">
          <img :src="oldhouseSrc" class="oldhouse scene-img" alt="" @click="goToTargetPage(12)">
          <img :src="shengdougongSrc" class="shengdougong scene-img" alt="" @click="goToTargetPage(5)">
          <img :src="WangyoujuSrc" class="Wangyouju scene-img" alt="" @click="goToTargetPage(7)">
        </div>
      </div>
    </div>
  </div>
  <footer class="mt-5">
    <div class="container">
      <div class="row">
        <p class="text-center">©2024 八斗故事村</p>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  name: 'NavbarComponent',
  props: {
    activePage: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      pageList: ["Home", "Story", "About"],
      pageName: ["八斗里簡介", "實境解謎", "製作團隊"],
      activePageNum: 0,
      blankMapSrc:  require("@/assets/sub-video/blank_map.png"),
      dapingSrc:  require("@/assets/sub-video/DapingShore.png"),
      dutiangongSrc:  require("@/assets/sub-video/Dutiangong.png"),
      fossilSrc:  require("@/assets/sub-video/fossil.png"),
      oldhouseSrc:  require("@/assets/sub-video/Oldhouse.png"),
      shengdougongSrc:  require("@/assets/sub-video/Shengdougong.png"),
      WangyoujuSrc:  require("@/assets/sub-video/Wangyouju.png")
    };
  },
  created() {
    this.activePageNum = this.activePage;
    console.log("Active Page Number is " + this.activePage);
    this.updateImageSrcs();
  },
  methods: {
    goToTargetPage(num) {
      this.$emit('updatePageIndex', num);
      this.activePageNum = num;
      this.$router.push({ name: 'SubPage' + num });
    },
    updateImageSrcs() {
      // 获取 localStorage 中的布尔值，假设有一个标志影响图片的选择
      let site1 = localStorage.getItem('var1') === 'true';
      let site2 = localStorage.getItem('var2') === 'true';
      let site3 = localStorage.getItem('var3') === 'true';
      let site4 = localStorage.getItem('var4') === 'true';
      let site5 = localStorage.getItem('var5') === 'true';
      let site6 = localStorage.getItem('var6') === 'true';

      // 根据布尔值切换图片的 src
      if (site6) {
        this.dapingSrc =  require("@/assets/sub-video/DapingShore_end.png");
      }
      if (site3) {
        this.dutiangongSrc = require("@/assets/sub-video/Dutiangong_end.png");
      }
      if (site4) {
        this.fossilSrc = require("@/assets/sub-video/fossil_end.png");
      }
      if (site5) {
        this.oldhouseSrc = require("@/assets/sub-video/Oldhouse_end.png");
      }
      if (site1) {
        this.shengdougongSrc = require("@/assets/sub-video/Shengdougong_end.png");
      }
      if (site2) {
        this.WangyoujuSrc = require("@/assets/sub-video/Wangyouju_end.png");
      }
    }
  }
}
</script>


